import React from "react";
import styles from "./order-details.module.css";
import image from "../../images/done.svg";
import {useSelector} from "../../services/hooks";


export const OrderDetails = () => {
  const { orderId } = useSelector((state) => state.orderDetails);

  return (
    <div className={`${styles.container} mt-30 mb-30`}>
      <p className="text text_type_digits-large" data-test={'order-number'} >{orderId}</p>
      <p className="text text_type_main-medium mt-8"> </p>
      <img className="mt-15" src={image} alt=""/>
      <p className="text text_type_main-default mt-15">   </p>
      <p className="text text_type_main-default text_color_inactive mt-2">    </p>
    </div>
  );
}
